* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

#parent {
  font-family: "Lora", serif;
  font-size: 13px;
}

@media (min-width: 992px) {
  #parent {
    display: grid;
    grid-template-columns: 350px auto;
  }
}

#parent h1 {
  font-size: 32px;
}

#parent h2 {
  font-size: 28px;
}

#parent h3 {
  font-size: 25px;
}

#parent h4 {
  font-size: 22px;
}

#parent h5 {
  font-size: 18px;
}

#parent h6 {
  font-size: 16px;
}

#parent h1, #parent h2, #parent h3, #parent h4, #parent h5, #parent h6 {
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
  color: #10887a;
  line-height: 1.5;
}

#parent p {
  margin: 10px 0;
}

#parent input {
  display: block;
  width: 100%;
  padding: 10px 19px;
  font-size: 15px;
  border-radius: 30px;
  border: none;
  box-shadow: none;
  margin: 10px 0;
}

#parent button {
  margin: 10px 0;
  display: block;
  width: 100%;
  padding: 13px 33px;
  font-size: 12px;
  border-radius: 30px;
  background-color: #16c4b0;
  border: none;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  cursor: pointer;
}

#parent button:focus, #parent button:active {
  box-shadow: none;
  outline: 0;
}

#parent .table {
  width: 100%;
  overflow-x: auto;
}

#parent .table table {
  border: 1px solid #dee2e6;
  border-collapse: collapse;
  width: 100%;
}

#parent .table table th {
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  border: 1px solid #dee2e6;
  border-bottom: 2px solid #dee2e6;
}

#parent .table table td {
  border: 1px solid #dee2e6;
}

#parent .table table th, #parent .table table td {
  padding: .75rem;
}

#parent .search p {
  margin: 10px 0 0;
}

#parent .search-button {
  display: grid;
  grid-template-columns: auto 50px;
}

#parent .search-button input {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

#parent .search-button button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  padding-left: 16px;
}

#parent #sidebar {
  background-color: #041221;
  color: #fff;
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: "Montserrat", sans-serif;
}

@media (min-width: 992px) {
  #parent #sidebar {
    padding: 30px !important;
    width: auto !important;
    height: 100vh;
    display: flex;
    grid-template-columns: 350px auto;
    position: sticky;
    top: 0;
  }
}

#parent #sidebar .logo {
  display: none;
  font-weight: 800;
  font-family: "Montserrat", sans-serif;
  font-size: 21px;
  margin: 0 0 20px;
}

@media (min-width: 992px) {
  #parent #sidebar .logo {
    display: inline-block;
  }
}

#parent #sidebar .logo span {
  color: #16c4b0;
}

#parent #sidebar .logo a {
  text-decoration: none;
  color: #fff;
}

#parent #sidebar .cover-image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: -2;
  opacity: 0.4;
}

#parent #sidebar button.closeBtn {
  background: none;
  color: #16c4b0;
  font-size: 25px;
  padding: 0;
  display: inline-block;
  width: 25px;
  margin: -9px 0 0;
  text-align: right;
  right: 30px;
  position: absolute;
}

@media (min-width: 992px) {
  #parent #sidebar button.closeBtn {
    display: none;
  }
}

#parent #sidebar button.closeBtn:hover {
  background-color: transparent;
  color: #10887a;
}

#parent #sidebar .avatar {
  width: 75px;
  height: 75px;
  margin-tOP: 30px;
}

#parent #sidebar .avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

#parent #sidebar .bio {
  line-height: 1.7;
  font-size: 13px;
  margin: 25px 0;
}

#parent #sidebar .social i {
  color: #fff;
  background-color: #16c4b0;
  border-radius: 50%;
  padding: 10px;
  margin-right: 2px;
  font-size: 16px;
}

#parent #sidebar .social i:hover {
  background-color: #10887a;
}

#parent #sidebar button:hover {
  background-color: #10887a;
}

#parent #sidebar .tags .tagName {
  display: block;
  text-decoration: none;
  color: #16c4b0;
  margin: 10px 0 0;
  text-transform: uppercase;
}

#parent #sidebar .tags .tagName:hover {
  color: #10887a;
}

#parent #main {
  padding: 70px 30px;
}

@media (min-width: 768px) {
  #parent #main {
    padding: 90px 30px;
  }
}

@media (min-width: 992px) {
  #parent #main {
    padding: 80px 100px;
  }
}

#parent #main .mobile-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

@media (min-width: 992px) {
  #parent #main .mobile-header {
    display: none;
  }
}

#parent #main .mobile-header button {
  margin: 25px 30px;
  padding: 0;
  background: none;
  color: #10887a;
  font-size: 25px;
  display: inline-block;
  width: auto;
}

#parent #main .mobile-header .logo {
  margin: 25px 30px;
  display: inline-block;
  font-weight: 800;
  font-family: "Montserrat", sans-serif;
  font-size: 21px;
  color: #041221;
  float: right;
}

#parent #main .mobile-header .logo span {
  color: #16c4b0;
}

#parent #main .mobile-header .logo a {
  color: #041221;
  text-decoration: none;
}

#parent #main .post-list {
  display: grid;
  grid-template-columns: auto;
  grid-gap: 70px;
}

@media (min-width: 1200px) {
  #parent #main .post-list {
    grid-template-columns: auto auto;
    grid-gap: 70px 100px;
  }
}

#parent #main .post-list .post-item .post-details p {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #464646;
}

#parent #main .post-list .post-item .post-details p i {
  padding-right: 5px;
}

#parent #main .post-list .post-item .post-details p.tags {
  margin-left: 20px;
  color: #10887a;
}

#parent #main .post-list .post-item .post-details p a {
  text-decoration: none;
  color: #10887a;
}

#parent #main .post-list .post-item .post-excerpt {
  line-height: 1.8;
  font-size: 16px;
  color: #464646;
}

#parent #main .post-list .post-item a.read-more {
  display: inline-block;
  border-radius: 30px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  text-decoration: none;
  width: auto;
  margin: 10px 0 0;
  background-color: transparent;
  color: #10887a;
  font-size: 13px;
  padding: 8px 16px;
  border: 1px solid #10887a;
}

#parent #main .post-list .post-item a.read-more:hover {
  background-color: #10887a;
  color: #fff;
}

#parent #main .load-btn {
  width: auto;
  margin: 30px 0 0;
  background: transparent;
  color: #10887a;
  border: none;
  padding: 10px 0;
  font-size: 20px;
  border-radius: 0;
}

#parent #main .load-btn .fas {
  font-size: 20px;
  padding: 0 9px;
}

#parent #main .post-article .post-title {
  text-align: center;
}

@media (min-width: 1200px) {
  #parent #main .post-article .post-title {
    margin: 0 100px 0;
  }
}

#parent #main .post-article .post-details {
  text-align: center;
}

#parent #main .post-article .post-details p {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #464646;
}

#parent #main .post-article .post-details p i {
  padding-right: 5px;
}

#parent #main .post-article .post-details p.tags {
  margin-left: 20px;
  color: #10887a;
}

#parent #main .post-article .post-details p a {
  text-decoration: none;
  color: #10887a;
}

#parent #main .post-article .post-body {
  margin: 30px 0;
  line-height: 1.8;
  font-size: 16px;
  color: #464646;
}

@media (min-width: 992px) {
  #parent #main .post-article .post-body {
    margin: 60px 0 100px;
  }
}

@media (min-width: 1200px) {
  #parent #main .post-article .post-body {
    margin: 60px 100px 100px;
  }
}

#parent #main .post-article .post-body p {
  margin: 30px 0;
}

#parent #main .post-article .post-body a {
  color: #10887a;
  text-decoration: none;
  font-weight: bold;
}

#parent #main .post-article .post-body h1, #parent #main .post-article .post-body h2, #parent #main .post-article .post-body h3, #parent #main .post-article .post-body h4, #parent #main .post-article .post-body h5, #parent #main .post-article .post-body h6 {
  color: #464646;
  margin: 30px 0;
}

#parent #main .post-article .post-body ol, #parent #main .post-article .post-body ul {
  margin: 30px 50px;
}

#parent #main .post-article .post-body blockquote {
  margin: 30px 0;
  padding: 10px 0 10px 30px;
  border-left: 5px solid #10887a;
}

#parent #main .post-article .post-body img {
  width: 100%;
}
